.post{
    width: 380px;
    margin: 0px 25px 40px 25px;
}
.pimg{
    width: 100%;
    height: 250px;
    object-fit: cover;
    border-radius:10px ;
}
.pinfo{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.pcat{
    font-family: 'Varela Round', sans-serif;
    font-size: 11px;
    color: #be9656;
    line-height: 20px;
    margin-top: 15px;
    cursor: pointer;
}
.ptitle{
    font-family: 'Josefin Sans', sans-serif;
    font-size: 24px;
    font-weight: 700;
    margin-top: 15px;
    cursor: pointer;
}

.pdate{
  font-style: italic;
  font-size: 13px;
  color: #999;
  margin-top: 10px;
    font-family: 'Lora', serif;
}

.pdesc{
    font-family: 'Varela Round', sans-serif;
    font-size: 14px;
    color: #444;
    line-height: 24px;
    margin-top: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
}